<template>
	<div class="panel">
		<h2>热销品牌Top5</h2>
		<div ref="barChart2" class="chart">图表</div>
		<div class="panel-footer"></div>
	</div>
</template>

<script>
	export default {
		name: 'BarPanel2',
		data() {
			return {
				listN: [],
				listV: [],
				listP: [],
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.barChart2);
				if (!chart) {
					chart = this.$echarts.init(this.$refs.barChart2);
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					grid: {
						top: "10%",
						bottom: "10%",
						left: "48px",
						right: "60px"
					},
					xAxis: {
						show: false
					},
					yAxis: [{
							type: 'category',
							inverse: true,
							data: this.listN,
							axisLine: {
								show: false
							},
							axisTick: {
								show: false,
							},
							axisLabel: {
								color: "#fff"
							}
						},
						{
							show: true,
							inverse: true,
							data: this.listV,
							axisLine: {
								show: false
							},
							axisTick: {
								show: false,
							},
							axisLabel: {
								fontSize: 12,
								color: "#fff"
							}
						}
					],
					series: [{
							name: '条',
							type: 'bar',
							data: this.listP,
							itemStyle: {
								borderRadius: 20,
								color: function(params) {
									return ["#1089E7", "#F57474", "#56D0E3", "#F8B448", "#8B78F6"][params
										.dataIndex
									];
								}
							},
							barCategoryGap: 50,
							barWidth: 10,
							label: {
								show: true,
								color: "white",
								position: "inside",
								formatter: function(params) {
									return params.value / 3 + "%"
								}
							},
							yAxisIndex: 0,
						},
						{
							name: '框',
							type: 'bar',
							barCategoryGap: 50,
							barWidth: 15,
							itemStyle: {
								color: "none",
								borderColor: "#00c1de",
								borderWidth: 3,
								borderRadius: 15
							},
							yAxisIndex: 1,
							data: [100, 100, 100, 100, 100],
						}
					]
				};
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			},
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'expend/brand.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.listN.push(resp.data[i][0]);
					this.listV.push(resp.data[i][1]);
				}
				this.$axios.get(process.env.BASE_URL + 'expend/brand2.dat').then(resp => {
					for (let i = 0; i < resp.data.length; i++) {
						this.listP.push(resp.data[i][1]*3);
					}
					this.drawChart();
				})
			})
		}
	}
</script>

